/* =====================================================
   16. Progress bar styles
   ===================================================== */

.progress-bar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    width: 0;
    box-shadow: 0 0 4px 0 rgba(0, 172, 193, 0.3);
    will-change: width, opacity;
    transition: width 0.2s, opacity 0.4s;
    opacity: 1;
    z-index: 999999;
}