/* =====================================================
   9. Site search styles
   ===================================================== */

.site-search {
    margin-bottom: 40px;
    height: $siteSearchHeight + px;

    $site-search: &;
    &__wrap {
        position: relative;
        z-index: 100;
    }
    &.--focus {
        .site-search__field {
            border-bottom-color: $accent-color;
        }
        .site-search__button {
            background-image: url(data:image/svg+xml;utf8;base64,);
        }
    }
    &.--searching {

    }
    &.--searched {
        .search-result {
            opacity: 1;
            visibility: visible;
        }
    }
    &__field {
        width: 100%;
        line-height: 19px;
        padding: 15px 22px 12px;
        border: none;
        border-bottom: 2px solid #f3f6fe;
        transition: border-color .13s ease-in;

        $placeholderColor: #b0bec5;
        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
            color: $placeholderColor;
        }
        &::-moz-placeholder { /* Firefox 19+ */
            color: $placeholderColor;
        }
        &:-ms-input-placeholder { /* IE 10+ */
            color: $placeholderColor;
        }
        &:-moz-placeholder { /* Firefox 18- */
            color: $placeholderColor;
        }
    }
    &__button {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 14px;
        height: 14px;
        background: url(data:image/svg+xml;utf8;base64,) no-repeat center/contain;
        border: none;
        outline: none;
        padding: 0;
        margin: auto;
        cursor: pointer;
        transition: background-image .13s ease-in;
        will-change: background-image;
    }
    .search-result {
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        min-height: 49px;
        border-radius: 4px;
        margin: 6px 0 0;
        padding: 8px 8px 2px;
        border: 1px solid #d9d9d9;
        box-shadow: 0 1px 0 0 rgba(0, 0, 0, .2), 0 2px 3px 0 rgba(0, 0, 0, .1);
        background: #ffffff;
        opacity: 0;
        visibility: hidden;
        transition: all .1s ease;

        a {
            color: $base-color;

            &:hover {
                color: $accent-color;
            }
        }
        &__item {
            position: relative;
            display: block;
            @include clearfix();
            margin-bottom: 8px;

            dt,
            dd {
                padding: 6px 10px;
                float: left;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
            }
            dt {
                width: 30%;
                text-align: right;
                position: relative;

                &:after {
                    content: "";
                    position: absolute;
                    top: 0;
                    height: 100%;
                    width: 1px;
                    background: #dddddd;
                    right: 0;
                }
            }
            dd {
                width: 70%;
            }
        }
        &__current {
            position: absolute;
            top: -5px;
            right: 10px;
            @include font-size(9px);
            color: $accent-color;
            letter-spacing: .3px;
        }
        &__not-found {
            text-align: center;
            padding: 6px;
        }
    }
}