.form {
    &__field {
        margin-bottom: 8px;

        &:last-child {
            margin-bottom: 0;
        }

        &__inner {
            position: relative;
        }
    }

    label {
        @include font-size(11px);
        display: block;
        margin-bottom: 4px;
    }

    input {
        @include font-size(11px);
        border: 2px solid #e6eaed;
        border-radius: 4px;
        padding: 0 10px;
        height: 34px;
        width: 100%;
        transition: border-color .13s ease;

        &:focus {
            border-color: $accent-color;
        }

        &.error {
            border-color: #FF3030;

            + .error-message {
                opacity: 1;
                visibility: visible;
                transform: translateY(-50%);
            }
        }
    }

    .error-message {
        display: flex;
        align-items: center;
        position: absolute;
        top: 50%;
        left: 100%;
        margin: 0 0 0 10px;
        padding: 10px;
        width: 180px;
        box-sizing: border-box;
        color: #3b4256;
        font-size: 12px;
        line-height: 1.28;
        background: #fff;
        box-shadow: 0 1.25rem 2.8125rem 0.9375rem rgba(44, 77, 138, 0.07);
        border-radius: 2px;
        transform: translateX(-0.625rem) translateY(-50%);
        opacity: 0;
        visibility: hidden;
        transition: .3s;
        z-index: 10;

        &:before {
            content: "";
            position: absolute;
            top: 50%;
            margin-top: -6px;
            left: -6px;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 6px 6px 6px 0;
            border-color: transparent #fff transparent transparent;
        }
    }
}