/* =====================================================
   17. Page status styles
   ===================================================== */

.admin-bar {
    .page-status {
        min-height: calc(100vh - #{$siteSearchHeight + $siteFooterHeight + $wpAdminBarHeight + 'px'});
    }
}

.page-status {
    display: table;
    width: 100%;
    min-height: calc(100vh - #{$siteSearchHeight + $siteFooterHeight + 'px'});
    @include clearfix();
    text-align: center;

    &__inner {
        display: table-cell;
        vertical-align: middle;
    }
    &__code {
        display: inline-block;
        width: 215px;
        height: 80px;
        margin-bottom: 25px;
        background: url("images/404.svg") no-repeat;
        font: 0px/0 a;
    }
    &__message {
        text-transform: uppercase;
    }
}