/* =====================================================
   15. Scroll to top toggle styles
   ===================================================== */

.scroll-to-top {
    display: none;
}

@media (min-width: $screen-sm-min) {
    .scroll-to-top {
        position: fixed;
        height: 60px;
        width: 60px;
        right: 30px;
        bottom: 30px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
        display: block;
        z-index: 10;
        overflow: hidden;
        background: $accent-color url(data:image/svg+xml;base64,) no-repeat center 50%;
        opacity: 0;
        visibility: hidden;
        transition: all .15s ease-in;

        &.--show {
            opacity: 0.5;
            visibility: visible;
        }
        &:hover {
            opacity: 1;
        }
    }
}