.showcases-list {
    margin-left: -15px;
    margin-right: -15px;

    > br {
        display: none;
    }

    &__item {
        padding: 0 15px;
        float: left;
        width: 27.5%;
        margin-bottom: 15px;
    }

    &__link {
        display: block;
        background: #f2f4f8;
        border-radius: 10px;
        padding: 25px 15px 20px;
        text-decoration: none;
        text-align: center;
        text-transform: uppercase;
        color: inherit;
        @include font-size(12px);
        transition-property: transform, box-shadow;
        transition-duration: .15s;

        [class^="i-"] {
            display: block;
            height: 100%;
            margin: 0 auto 8px;
        }

        .i-code-snippet {
            width: 64px;
            height: 64px;
            background: url("images/require.svg") no-repeat center/contain;
        }

        &:hover {
            transform: translateY(-2px);
            box-shadow: 0 12px 30px rgba(50, 50, 93, .1), 0 3px 12px rgba(0, 0, 0, .07);
        }
    }

    @include clearfix();
}