/* =====================================================
   11. Page navigation styles
   ===================================================== */

.page-nav {
    padding: 32px 0;
    height: $pageNavHeight + px;

    a {
        @include font-size(16px);
        font-family: $base-font-bold;
        text-decoration: none;
        display: inline-block;
        vertical-align: middle;

        &:after {
            content: "";
            width: 16px;
            height: 16px;
            display: inline-block;
            vertical-align: middle;
            margin-left: 8px;
            background: url(data:image/svg+xml;base64,) no-repeat left center/contain;
            transition: margin-left .13s ease-in;
        }

        &:hover {
            &:after {
                margin-left: 11px;
            }
        }
    }
}