/* =====================================================
   4. Sidebar styles
   ===================================================== */

.sidebar {
    position: fixed;
    width: 420px;
    height: 100%;
    overflow: auto;
    left: 0;
    top: 0;
    background: #f5f6f9;
    box-shadow: 0 2px 18px 0 #e3e7f0 inset;
    padding: 40px 20px 30px 110px;
    transform: translateX(0);
    transition: width .3s ease-in, transform .3s ease-in;
    will-change: width, transform;
    z-index: 10;

    &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
    }
    &::-webkit-scrollbar-button {
        width: 8px;
        height: 8px;
    }
    &::-webkit-scrollbar-thumb {
        background: #e1e1e1;
        border: 18px none #ffffff;
        border-radius: 10px;
    }
    &::-webkit-scrollbar-track {
        background: #CCCCCC;
        border: 36px none #ffffff;
        border-radius: 30px;
    }
    &::-webkit-scrollbar-corner {
        background: transparent;
    }
}

.admin-bar {
    .sidebar {
        top: 32px;
    }
}

@media (max-width: $screen-lg-max) {
    .sidebar {
        transform: translateX(-300px);
        width: 300px;
        padding: 35px 20px 20px 35px;
    }
    .--sidebar-show {
        .sidebar {
            transform: translateX(0);
        }
    }
}