/* =====================================================
   10. Breadcrumbs styles
   ===================================================== */

.breadcrumbs {
    padding: 20px 0;
    height: $breadcrumbsHeight + px;

    .separator {
        width: 5px;
        height: 9px;
        background: url(data:image/svg+xml;base64,) no-repeat left center/contain;
        display: inline-block;
        vertical-align: middle;
        margin: 0 5px;
    }
    &__item {
        @include font-size(11px);
        color: #90a4ae;
        text-decoration: none;
        padding: 4px 0;

        &:not(:last-child) {
            &:hover {
                color: #149db4;
            }
        }
        &:last-child {
            color: #2c3549;
        }
    }
}