/* =====================================================
   13. How to contact section styles
   ===================================================== */


.how-to-contact {
    &__caption {
        @include font-size(16px);
        font-family: $base-font-medium;
        margin-bottom: 12px;
    }
    &__text {
        line-height: 1.8461;
    }
    &__phone {
        font-family: $base-font-bold;
        color: #2c3549;
        text-decoration: none;
        white-space: nowrap;
    }
}