.section-nav-list {
    margin-left: -7px;
    margin-right: -7px;

    > br {
        display: none;
    }

    &__item {
        padding: 0 7px;
        float: left;
        width: 50%;
        margin-bottom: 14px;
    }

    &__link {
        background: #f2f4f8;
        border-radius: 10px;
        padding: 10px;
        text-decoration: none;
        font-family: $base-font-bold;
        color: inherit;
        @include font-size(15px);
        line-height: 1.125;
        height: 98px;
        display: flex;
        align-items: center;
        transition-property: transform, box-shadow;
        transition-duration: .15s;

        [class^="i-"] {
            display: block;
            min-width: 72px;
            height: 40px;
        }

        .i-person {
            background: url("images/reg.svg") no-repeat center/contain;
        }

        .i-equalizer {
            background: url("images/setup.svg") no-repeat center/contain;
        }

        .i-mobile-notice {
            background: url("images/mobile.svg") no-repeat center/contain;
        }

        .i-lego {
            background: url("images/module.svg") no-repeat center/contain;
        }

        .i-code-snippet {
            background: url("images/require.svg") no-repeat center/contain;
        }

        &:hover {
            transform: translateY(-2px);
            box-shadow: 0 12px 30px rgba(50,50,93,.1), 0 3px 12px rgba(0,0,0,.07);
        }
    }

    @include clearfix();
}