.card-list {
    display: flex;
    margin-bottom: 20px;
    justify-content: space-between;

    &__caption {
        margin-bottom: 15px;
        display: block;
    }

    &__item {
        width: 308px;

        &:first-child {
            .card {
                margin-left: -13px;
            }
        }

        &:last-child {
            .card {
                margin-right: -13px;
            }
        }
    }

    &__msg {
        margin: 16px;
        display: flex;

        dd {
            margin-left: 46px;
            font-family: $base-font-bold;
        }
    }
}

.card {
    background-color: #00a4b9;
    border-radius: 1rem;
    box-shadow: 0 4px 24px 0 rgba(0,0,0,.16);
    padding: 24px 23px 47px 28px;
    text-align: left;
    font-size: 0;
    height: 194px;
    transition: background-color .3s cubic-bezier(.4,0,1,1);

    &__mps {
        height: 14px;
        text-align: right;
        margin-bottom: 4px;

        &__icon {
            height: inherit;
            display: inline-block;
            width: 13%;
            animation-duration: .75s;
            animation-fill-mode: none;

            &.visa {
                animation-name: fadeInLeft;
                background: url("data:image/svg+xml;base64,") no-repeat center/cover;
            }

            &.mc {
                animation-name: fadeInLeft;
                background: url("data:image/svg+xml;base64,") no-repeat center/auto 100%;
            }
        }
    }

    label {
        display: block;
        font-size: 11px;
        color: #fff;
        padding: 0 0 2px 0;
    }

    input {
        display: block;
        font-size: 21px;
        color: #fff;
        font-family: LatoBold,sans-serif;
        background: transparent;
        padding: 0 10px 0 3px;
        width: 100%;
        border: none;
        line-height: 1.45;
    }

    &__number {
        margin-bottom: 19px;

        &__inner {
            width: 85%;
            border-bottom: 1px solid rgba(255, 255, 255, 0.7);
            position: relative;
        }
    }

    &__expire-date {
        margin: 0 20px 0 0;

        &__inner {
            width: 70%;
            border-bottom: 1px solid rgba(255, 255, 255, 0.7);
        }
    }

    &__expire-date,
    &__cvc {
        display: inline-block;
        vertical-align: top;
        width: 30%;
        position: relative;

        label {
            padding-bottom: 5px;
            display: flex;
            align-items: center;
        }

        input {
            font-size: 14px;
        }
    }

    &__cvc {
        &__inner {
            width: 70%;
            border-bottom: 1px solid rgba(255, 255, 255, 0.7);
        }
    }

    .tooltip {
        display: inline-block;
        margin-left: 10px;
        border-radius: 50%;
        color: #00a4b9;
        background: url("images/help.svg") no-repeat 0 0/100% auto;
        position: relative;
        cursor: pointer;
        text-align: center;
        line-height: 9px;
        width: 9px;
        height:9px;
        padding: 0;

        &__content {
            position: absolute;
            top: -15px;
            left: 100%;
            margin: 0 0 0 10px;
            padding: 10px;
            width: 185px;
            box-sizing: border-box;
            color: #3b4256;
            font-size: 12px;
            line-height: 1.45;
            background: #fff;
            box-shadow: 0 1.25rem 2.8125rem 0.9375rem rgba(44, 77, 138, 0.07);
            border-radius: 2px;
            transform: translateX(-0.625rem);
            opacity: 0;
            visibility: hidden;
            transition: .3s;
            z-index: 10;

            &:before {
                content: "";
                position: absolute;
                top: 14px;
                left: -6px;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 6px 6px 6px 0;
                border-color: transparent #fff transparent transparent;
            }
        }

        &:hover {
            .tooltip__content {
                visibility: visible;
                opacity: 1;
                transform: translateX(0);
            }
        }
    }
}

@keyframes fadeInLeft {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(-100%, 0, 0);
        transform: translate3d(-100%, 0, 0)
    }
    to {
        opacity: 1;
        -webkit-transform: none;
        transform: none
    }
}

@media (max-width: $screen-sm-max) {
    .card-list {
        display: block;

        &__item {
            margin-bottom: 15px;
        }
    }
}