.button {
    display: inline-block;
    border: none;
    outline: none;
    background: #00c0d5;
    height: 42px;
    line-height: 42px;
    text-transform: uppercase;
    font-family: $base-font-bold;
    letter-spacing: 0.1px;
    color: #FFFFFF;
    padding: 0 15px;
    border-radius: 8px;
    min-width: 185px;
    cursor: pointer;
    transition: all .13s ease-in;

    &:hover {
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.22);
    }
}