.table {
    background: #f8fcfd;
    margin: 0 -24px 25px;
    padding: 14px 24px;
    @include clearfix();

    table {
        width: 100% !important;
        margin: 0;
        float: none;

        tr {
            &:not(:last-child) {
                th,
                td {
                    border-bottom: 1px solid #e6e9f1;
                }
            }
        }
        th,
        td {
            line-height: 1.54;
            padding: 12px;

            &:first-child {
                padding-left: 24px;
            }
        }
        th {
            color: #78909c;
        }
        code {
            word-break: break-all;
        }
    }
}

@media (max-width: $screen-sm-max) {
    .table {
        &__inner {
            overflow: auto;
            &::-webkit-scrollbar {
                width: 2px;
                height: 2px;
            }
            &::-webkit-scrollbar-thumb {
                border-radius: 10px;
                background: #CCCCCC;
            }
            &::-webkit-scrollbar-track {
                border-radius: 10px;
                background: #eeeeee;
            }
        }

        table {
            table-layout: fixed;
        }
    }
}