.payment-frame {
    &--wrap {
        background: #fafbfe;
        border-radius: 8px;
        padding: 15px 16px 25px;
        margin-bottom: 20px;
    }

    &__tabs {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 5px;

        &__item {
            margin: 0;

            a {
                display: flex;
                align-items: center;
                padding: 18px;
                margin: 10px;
                text-decoration: none;
                text-transform: uppercase;
                color: #000000;
                background: #FFFFFF;
                border-radius: 8px;
                @include font-size(10px);
                line-height: 1.5;
                width: 160px;
                box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1);
                user-select: none;

                .i-popup-frame {
                    width: 32px;
                    height: 32px;
                    background: url("images/frame-pop.svg") no-repeat center/contain;
                    margin-right: 4px;
                }

                .i-inbuilt-frame {
                    width: 32px;
                    height: 32px;
                    background: url("images/require.svg") no-repeat center/contain;
                    margin-right: 4px;
                }

                &.active {
                    background: #00c0d5;
                    color: #FFFFFF;
                    cursor: default;
                    pointer-events: none;

                    .i-popup-frame {
                        background-image: url("images/frame-pop-wh.svg");
                    }

                    .i-inbuilt-frame {
                        background-image: url("images/require-wh.svg");
                    }
                }
            }
        }
    }

    &__content {
        margin-bottom: 22px;
        display: flex;
        width: 100%;

        &__col {
            margin: 0 8px;
            vertical-align: top;
            width: 45%;

            &.code-snippet--wrap {
                margin-top: 18px;
                width: 55%;
                max-height: 408px;
                background: #f3f5f8;
                border-radius: 8px;
            }
        }

        .code-snippet {
            margin: 0;
            padding: 16px 38px 16px 22px;
            height: 100%;

            pre {
                padding: 0;
                white-space: pre-wrap;
                height: 100%;
            }
        }
    }

    &__controls {
        text-align: center;
    }

    &__inbuilt {
        position: relative;
        margin-bottom: 0;

        &__area {
            width: 400px;
            height: 690px;
            margin: 15px auto 0;
        }

        .preloader--wrap {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: #fafbfe;
        }

        .preloader {
            position: absolute;
            width: 80px;
            height: 80px;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            margin: auto;
            transform: rotate(-90deg);
            transition: all .15s ease;

            &__spinner-track {
                fill: transparent;
                stroke: #00c2d7;
                stroke-width: 20px;
            }

            &__spinner {
                fill: transparent;
                stroke: rgba(0,0,0,0.3);
                stroke-width: 20px;
                stroke-dasharray: 650px;
                stroke-dashoffset: 0;
                animation: spin 2s linear infinite;
            }
        }
    }
}

@keyframes spin {
    0% {
        stroke-dashoffset: 650px;
    }
    50% {
        stroke-dashoffset: 0;
    }
    100% {
        stroke-dashoffset: -650px;
    }
}

@media (max-width: $screen-sm-max) {
    .payment-frame {
        &__content {
            display: block;

            &__col {
                width: 360px !important;
                max-width: 100%;
                height: auto !important;
                margin: 0 auto;
            }
        }
    }
}

@media (max-width: $screen-sm-max - 200px) {
    .payment-frame {
        &__tabs {
            display: block;

            &__item {
                a {
                    margin: 0 auto 10px;
                }
            }
        }
    }
}