// Main colors
$base-color: #2c3549;
$accent-color: #00acc1;

// Font configs
$base-font-size: 13px;
$base-line-height: 1.5384;

// Font families
$base-font-bold: 'LatoBold', sans-serif;
$base-font-medium: 'LatoMedium', sans-serif;
$base-font-regular: 'LatoRegular', sans-serif;
$code-font-regular: 'PTMonoRegular', monospace;
$code-font-bold: 'PTMonoBold', monospace;

// Base transition time
$transition-time: .13s 0s ease-in;

// Height for sticky footer
$siteSearchHeight: 48;
$breadcrumbsHeight: 60;
$pageNavHeight: 88;
$siteFooterHeight: 148;
$wpAdminBarHeight: 32;

// Screens resolutions
$screen-sm-min: 768px;
$screen-sm-max: 767px;
$screen-md-min: 992px;
$screen-md-max: 991px;
$screen-lg-min: 1280px;
$screen-lg-max: 1279px;