/* =====================================================
   8. Site navigation styles
   ===================================================== */

.menu {
    max-width: 230px;

    .sub-menu {
        margin: 0 0 0 8px;
        padding-bottom: 0;
        line-height: normal;
        display: none;
    }
    .depth-1 {
        > .menu-item__inner {
            font-family: $base-font-bold;
            @include font-size(16px);
            line-height: 1.125;
            letter-spacing: 0.4px;
            color: #2c3549;
            margin-bottom: 24px;
        }
    }
    .depth-2 {
        > .menu-item__inner {
            font-family: $base-font-medium;
            line-height: 1.3846;
            letter-spacing: 0.9px;
            text-transform: uppercase;
            color: #868ea2;
            margin-bottom: 20px;
        }
        &:last-child {
            > .menu-item__inner {
                margin-bottom: 16px;
            }
        }
        &:not(.current-menu-ancestor).current-menu-item {
            > .menu-item__inner {
                color: $accent-color;
            }
        }
    }
    .depth-3 {
        > .menu-item__inner {
            font-family: $base-font-medium;
            line-height: 1.3846;
            letter-spacing: 0.2px;
            color: #2c3549;
            margin-bottom: 16px;

            &:hover {
                color: $accent-color;
            }
        }
        > .sub-menu {
            margin-left: 17px;
        }
        &.current-menu-item {
            > .menu-item__inner {
                color: $accent-color;
            }
        }
    }
    .depth-4 {
        > .menu-item__inner {
            font-family: $base-font-medium;
            line-height: 1.2307;
            letter-spacing: 0.2px;
            color: #868ea2;
            margin-bottom: 16px;
        }
        &:last-child {
            > .menu-item__inner {
                margin-bottom: 24px;
            }
        }
        &.current-menu-item {
            > .menu-item__inner {
                color: $accent-color;
            }
        }
    }
    &-item {
        margin-bottom: 0;

        > .menu-item__inner {
            text-decoration: none;
            display: inline-block;
            will-change: color;
            transition: color .13s ease-in;
        }
        &.current-menu-ancestor,
        &.current-menu-item {
            > .sub-menu {
                display: block;
            }
        }
    }
}