/* =====================================================
   6. Content section styles
   ===================================================== */

.admin-bar {
    .content {
        min-height: calc(100vh - #{$siteSearchHeight + $breadcrumbsHeight + $pageNavHeight + $siteFooterHeight + $wpAdminBarHeight + 'px'});
    }
}

.content {
    min-height: calc(100vh - #{$siteSearchHeight + $breadcrumbsHeight + $pageNavHeight + $siteFooterHeight + 'px'});
    @include clearfix();
}

@media (max-width: $screen-sm-max) {
    .content,
    .admin-bar .content {
        min-height: 0;
    }
}