/* =====================================================
   18. Search result styles
   ===================================================== */

$margin: 20;

.admin-bar {
    .search-result {
        min-height: calc(100vh - #{$siteSearchHeight + $siteFooterHeight + $wpAdminBarHeight + $margin + $margin + 'px'});
    }
}

.search-result {
    margin-top: $margin + px;
    margin-bottom: $margin + px;
    min-height: calc(100vh - #{$siteSearchHeight + $siteFooterHeight + 'px'});
    @include clearfix();

    &__item {
        margin-bottom: 20px;

        &:not(:last-child) {
            padding-bottom: 20px;
            border-bottom: 1px dotted #AAAAAA;
        }
    }
    &__not-found-caption {
        margin-bottom: 10px;
    }
}