/* =====================================================
   2. Global styles
   ===================================================== */
@import "fonts-init";

* {
    &::selection {
        background: lighten($accent-color, 10%);
        color: #ffffff;
    }
    &::-moz-selection {
        background: lighten($accent-color, 10%);
        color: #ffffff;
    }
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

html,
input,
button,
select,
textarea {
    font: #{$base-font-size}/#{$base-line-height} $base-font-regular;
    color: $base-color;

    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
}

body {
    font: inherit;
    color: inherit;
    min-width: 320px;
    overflow-x: hidden;
}

a {
    color: $accent-color;
    outline: none;

    &:hover,
    &:focus {
        text-decoration: none;
    }
}

img {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 0;
}

input,
textarea {
    &:focus {
        outline: none;
    }
}

input::-ms-clear {
    display: none;
}

.hidden,
.screen-reader-text {
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    position: absolute !important;
    left: -9999px;
    top: -9999px;
    width: 1px;
}

[data-ajaxify-transition] {
    transition: opacity .1s ease-in, visibility .1s ease-in;
    will-change: opacity, visibility;
}