.code-snippet {
    background: #f3f5f8;
    border-radius: 8px;
    display: block;
    padding: 14px 44px 14px 24px;
    font-family: $code-font-regular;
    @include font-size(10px);
    line-height: 1.67;
    letter-spacing: 0.6px;
    margin: 0 -24px 25px;
    position: relative;

    pre {
        overflow: auto;
        padding: 10px 0;

        &::-webkit-scrollbar {
            width: 2px;
            height: 2px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 10px;
            background: #CCCCCC;
        }
        &::-webkit-scrollbar-track {
            border-radius: 10px;
            background: #eeeeee;
        }
    }
    strong,
    b {
        font-family: $code-font-bold;
    }
    &__copy-toggle {
        width: 16px;
        height: 16px;
        position: absolute;
        top: 12px;
        right: 12px;
        background: url(data:image/svg+xml;base64,) no-repeat center/contain;
        border: none;
        outline: none;
        cursor: pointer;

        &::-moz-focus-inner {
            border: 0;
        }
        &.--copied {
            .code-snippet__tooltip {
                opacity: 1;
                visibility: visible;
            }
        }
    }
    &__tooltip {
        position: absolute;
        bottom: 100%;
        width: 120px;
        left: 50%;
        margin-left: -60px;
        background: #ffffff;
        box-shadow: 0 2px 18px 0 #e3e7f0;
        border-radius: 5px;
        font-family: $base-font-regular;
        @include font-size(9px);
        line-height: 1.6;
        text-align: center;
        padding: 10px 12px;
        white-space: normal;
        visibility: hidden;
        opacity: 0;
        transition: all .13s ease-in;

        &:after {
            content: "";
            width: 0;
            height: 0;
            position: absolute;
            top: 100%;
            left: 0;
            right: 0;
            margin: auto;
            border-left: 4px solid transparent;
            border-right: 4px solid transparent;
            border-top: 5px solid #ffffff;
        }
    }
}