/* =====================================================
   3. Typography styles
   ===================================================== */

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 0;
    padding-top: 6px;
    margin-bottom: 12px;
    font-family: $base-font-medium;
    font-weight: normal;
}

h1 {
    @include font-size(32px);
    line-height: 1.2187;
}

h2 {
    @include font-size(20px);
    line-height: 1.2;
}

h3 {
    @include font-size(16px);
    line-height: 1.1875;
}

h4 {
    @include font-size(14px);
    line-height: 1.1666;
}

h5 {
    @include font-size(12px);
    line-height: 1.1666;
}

h6 {
    @include font-size(10px);
    line-height: 1.1666;
}

b,
strong {
    font-weight: normal;
    font-family: $base-font-bold;
}

small {
    @include font-size(11px);
}

em {
    font-style: italic;
}

code {
    font-family: $code-font-regular;
    @include font-size(10px);

    b,
    strong {
        font-family: $code-font-bold;
    }
}

p {
    margin-bottom: 16px;
}

ul,
ol {
    margin-bottom: 24px;
    padding-bottom: 1px;

    li {
        line-height: 1.5384;
        margin-bottom: 5px;
    }
    &.list-style-star {
        li {
            position: relative;
            display: table;
            margin-bottom: 10px;

            &:before {
                content: "";
                display: table-cell;
                width: 36px;
            }
            &:after {
                content: "";
                background: url(data:image/svg+xml;utf8;base64,) no-repeat center/contain;
                width: 16px;
                height: 16px;
                position: absolute;
                top: 50%;
                left: 6px;
                margin-top: -10px;
            }
        }
    }
}

ol {
    counter-reset: myCounter;

    li {
        list-style: none;
        margin-bottom: 10px;
        display: table;
        position: relative;

        &:before {
            content: "";
            display: table-cell;
            width: 36px;
        }
        &:after {
            counter-increment: myCounter;
            content: counter(myCounter);
            position: absolute;
            left: 0;
            top: -3px;
            width: 24px;
            height: 24px;
            background: #00acc1;
            @include font-size(11px);
            line-height: 24px;
            text-align: center;
            letter-spacing: 0.8px;
            color: #ffffff;
            border-radius: 50%;
        }
    }
}

@import "../base/tinymce";
@import "../components/code-snippet";
@import "../components/table";
